  list-style: none;
}

.side-nav li a:link,
.side-nav li a:visited {
  padding: 8px 0;
  color: rgb(12, 12, 12);
  text-decoration: none;
  font-size: 90%;
  border-bottom: 2px solid transparent;
  -webkit-transition: border-bottom 0.2s;
