/* Media queries */

/* Big tablet to 1200px (widths smaller taht the 1140px row) */
@media only screen and (max-width: 1200px) {
  .hero-text-box {
      width: 100%;
      padding: 0 2%;
  }
  
  .row { padding: 0 2%; }
}


/* Small tablet to big tablet: from 768px to 1023px */
@media only screen and (max-width: 1023px) {
  body { font-size: 18px; }
  section { padding: 60px 0; }
  
  
  .icon-small { 
      width: 17px;
      margin-right: 5px;
  }
  
  
  .plan-box {
      width: 100%;
      margin-left: 0%;
  }
  
  .contact-form { width: 80%; }


}



/* Small phones to small tablets: from 481px to 767px */
@media only screen and (max-width: 767px) {
  body { font-size: 16px; }
  section { padding: 30px 0; }
  
  .row,
  .hero-text-box { padding: 0 4%; }
  .col { 
      width: 100%;
      margin: 0 0 4% 0;
  }
  
  .main-nav { display: none; }
  .homePagelink  { display: none; }
  .mobile-nav-icon {display: inline-block;}
  
  .mobile-nav-icon { margin-top: 10px; }
  .mobile-nav-icon i { color: rgb(233, 230, 230); }

  .mobile-nav-icon-black {display: inline-block;}
  
  .mobile-nav-icon-black { margin-top: 10px; }
  .mobile-nav-icon-black i { color:  #e67e22;; }
  
  
  
  h1 { font-size: 180%; }
  h2 { font-size: 150%; }

  .message {
    width: 250px;
    height: 75px;
  }

  .logo {
    height: 50px;
    width: auto;
    float: left;
  }
  
}



/* Small phones: from 0 to 480px */
@media only screen and (max-width: 480px) {
  section { padding: 25px 0; }
  .contact-form {width: 100%; }

  .hero-text-box {
    width: 100%;
    padding: 0 0.5%;
}
}

/* Spinner */

.sk-chase {
  width: 40px;
  height: 40px;
  position: relative;
  animation: sk-chase 2.5s infinite linear both;
}

.sk-chase-dot {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0; 
  animation: sk-chase-dot 2.0s infinite ease-in-out both; 
}

.sk-chase-dot:before {
  content: '';
  display: block;
  width: 25%;
  height: 25%;
  background-color: #e67e22;
  border-radius: 100%;
  animation: sk-chase-dot-before 2.0s infinite ease-in-out both; 
}

.sk-chase-dot:nth-child(1) { animation-delay: -1.1s; }
.sk-chase-dot:nth-child(2) { animation-delay: -1.0s; }
.sk-chase-dot:nth-child(3) { animation-delay: -0.9s; }
.sk-chase-dot:nth-child(4) { animation-delay: -0.8s; }
.sk-chase-dot:nth-child(5) { animation-delay: -0.7s; }
.sk-chase-dot:nth-child(6) { animation-delay: -0.6s; }
.sk-chase-dot:nth-child(1):before { animation-delay: -1.1s; }
.sk-chase-dot:nth-child(2):before { animation-delay: -1.0s; }
.sk-chase-dot:nth-child(3):before { animation-delay: -0.9s; }
.sk-chase-dot:nth-child(4):before { animation-delay: -0.8s; }
.sk-chase-dot:nth-child(5):before { animation-delay: -0.7s; }
.sk-chase-dot:nth-child(6):before { animation-delay: -0.6s; }

@keyframes sk-chase {
  100% { transform: rotate(360deg); } 
}

@keyframes sk-chase-dot {
  80%, 100% { transform: rotate(360deg); } 
}

@keyframes sk-chase-dot-before {
  50% {
    transform: scale(0.4); 
  } 100%, 0% {
    transform: scale(1.0); 
  } 
}

.spinnerCenter {
  display: flex;  
  justify-content: center;
  font-size: 150%;
  padding: 1%;
}

/* Floating SideNav */
.floatContainer {
  display: flex; 
  justify-content: space-between;
}

.subHeadingContainer {
  display: flex; 
  justify-content: space-around;
}

.homePagelink {
  list-style: none;
}

select {
  /* styling */
  background-color: white;
  border: thin solid rgb(186, 186, 190);
  border-radius: 4px;
  display: inline-block;
  font: inherit;
  line-height: 1em;
  padding: 0.5em 3.5em 0.5em 1em;

  /* reset */

  margin: 0;      
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  -webkit-appearance: none;
  -moz-appearance: none;
}


/* arrows */

.classic select {
  background-image:
    linear-gradient(45deg, transparent 50%, #e67e22 50%),
    linear-gradient(135deg, #e67e22 50%, transparent 50%),
    linear-gradient(to right, rgba(231, 228, 224, 0.979), rgba(230, 223, 218, 0.979));
  background-position:
    calc(100% - 20px) calc(1em + 2px),
    calc(100% - 15px) calc(1em + 2px),
    100% 0;
  background-size:
    5px 5px,
    5px 5px,
    2.5em 2.5em;
  background-repeat: no-repeat;
}

.classic select :focus {
  background-image:
    linear-gradient(45deg, white 50%, transparent 50%),
    linear-gradient(135deg, transparent 50%, white 50%),
    linear-gradient(to right, gray, gray);
  background-position:
    calc(100% - 15px) 1em,
    calc(100% - 20px) 1em,
    100% 0;
  background-size:
    5px 5px,
    5px 5px,
    2.5em 2.5em;
  background-repeat: no-repeat;
  border-color: grey;
  outline: 0;
}




